@import url(http://fonts.googleapis.com/css?family=Lato&subset=latin,latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  
  font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-size: 62.5%;
  line-height: 1.7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header{font-size:1.2rem;margin:0;box-sizing:border-box;display:flex;justify-content:space-between;align-items:center;padding:0 3rem;height:4rem;color:white;position:fixed;width:100%;background-color:transparent;z-index:3}.header__content{height:100%;display:flex}.header__content a{width:7rem;text-decoration:none;color:white;display:flex;align-items:center;justify-content:center;margin:0 auto;height:100%;border-radius:7px;transition:all .5s}.header__content a:hover{background-color:lightgray;color:darkgray;box-shadow:0 2rem 5rem rgba(0,0,0,0.1);cursor:pointer}

.specific-project{font-size:1rem;box-sizing:border-box;padding:5rem 7vw;position:absolute}.specific-project__border{height:24.5rem;top:9.5rem}.specific-project__params{height:25rem;position:relative;display:grid;grid-template-columns:repeat(auto-fit, minmax(19rem, -webkit-min-content));grid-template-columns:repeat(auto-fit, minmax(19rem, min-content));grid-gap:2rem;justify-content:center;overflow-y:scroll}.overview{box-sizing:border-box;display:flex;flex-direction:row;padding:4vw;text-align:justify}.overview__info{width:50vw}.overview__image{width:20rem;height:20rem;object-fit:cover;margin:0 auto;margin-left:2rem;border-radius:5px;box-shadow:0 2rem 5rem rgba(0,0,0,0.1);transition:all .2s}.overview__image:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.parametersField{margin-bottom:1rem;position:relative;top:0}.parameter{display:flex;flex-direction:column;max-width:100%;margin:1rem;text-align:left}.parameter__desc{margin:0;display:flex;flex-direction:row;align-items:flex-start;padding-right:1rem;justify-content:space-between}.parameter__desc-input{border:none;border-bottom:1px solid gray;text-align:center}.parameter__desc input:focus{outline:none}.parameter__desc-title{padding:.1rem;font-size:0.8rem;max-width:50%;word-wrap:break-word;-webkit-hyphens:auto;-ms-hyphens:auto;hyphens:auto}.parameter__text{width:100%;height:-webkit-min-content;height:min-content;font-size:0.8rem;font-weight:200;margin:0}

.loader{height:100%;width:100%;background-color:rgba(0,0,0,0.5);display:flex;align-items:center}.loader__icon{border:1rem solid #f3f3f3;border-top:1rem solid #3498db;border-radius:50%;width:6rem;height:6rem;-webkit-animation:spin 1s cubic-bezier(1, 0.01, 0.21, 1.04) infinite;animation:spin 1s cubic-bezier(1, 0.01, 0.21, 1.04) infinite;margin:auto}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.button{font-size:1.3rem;border:none;border-radius:15px;padding:1rem;box-shadow:0 2rem 5rem rgba(0,0,0,0.015);background-color:lightgray}.button:hover{background-color:darkgray;cursor:pointer}

.aiResult{position:absolute;height:100%;width:100%;background-color:rgba(0,0,0,0.7);-webkit-transform:scale(0.3);transform:scale(0.3);transition:all .7s;z-index:3;top:0;left:0;border-radius:10px;color:white;display:flex;flex-direction:column;justify-content:center;opacity:0;visibility:hidden}.aiResult__active{-webkit-transform:scale(1);transform:scale(1);opacity:1;visibility:visible}

section{height:95vh;width:100vw}.homePage{position:relative;top:0;left:0;z-index:1}.headerSection{background-size:cover;background-position:center;-webkit-clip-path:polygon(0 0, 100% 0, 100% 100%, 0 80vh);clip-path:polygon(0 0, 100% 0, 100% 100%, 0 80vh);display:flex;flex-direction:column;justify-content:center;align-items:center;color:lightgray}.headerSection__name{margin:0 5rem;font-size:2rem;background-image:linear-gradient(to right bottom, lightgray, gray);-webkit-background-clip:text}.headerSection__description{margin:0 5rem;font-size:1.5rem;font-weight:100}.aboutSection{-webkit-clip-path:polygon(0 0, 100% 15vh, 100% 100%, 0 80vh);clip-path:polygon(0 0, 100% 15vh, 100% 100%, 0 80vh);color:white;font-size:1.6rem;padding:5rem 2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(30rem, -webkit-min-content));grid-template-columns:repeat(auto-fit, minmax(30rem, min-content));justify-content:center;grid-gap:1rem;text-align:center}.aboutSection__skills{display:flex;flex-direction:column;text-align:center}.aboutSection__skills-list{display:flex}.aboutSection ul{text-align:left;margin-left:2rem}.aboutSection li{list-style:none;margin-left:2rem}.aboutSection li.python{list-style:square}.proyectsSection{-webkit-clip-path:polygon(0 0, 100% 15vh, 100% 100%, 0 100%);clip-path:polygon(0 0, 100% 15vh, 100% 100%, 0 100%);padding-top:8rem;color:white;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-height:100vh;display:flex;flex-direction:column}.proyectsSection__description{font-size:1.6rem}.proyectsSection__categoryContainer{font-size:1rem;display:flex;justify-content:center;padding-bottom:1rem;max-width:100%;position:relative}.proyectsSection__categoryContainer ul{display:flex;flex-direction:row;flex-wrap:wrap;text-decoration:none;list-style:none;justify-content:center}.proyectsSection__categoryContainer-item{margin-left:1rem;cursor:pointer;transition:-webkit-transform 2s;transition:transform 2s;transition:transform 2s, -webkit-transform 2s;min-width:3rem}.proyectsSection__categoryContainer-item::after{content:"";border-bottom:3px solid white;top:100%;left:50%;height:3px;width:1px}.proyectsSection__categoryContainer-item.active{border-bottom:3px solid white}.proyectsSection__projects{display:grid;grid-template-columns:repeat(auto-fit, minmax(25rem, -webkit-min-content));grid-template-columns:repeat(auto-fit, minmax(25rem, min-content));grid-gap:1rem;justify-content:center}.proyectsSection__projects.loading{margin:0 15rem}

.card{font-size:1rem;height:13rem;width:25rem;border-radius:3px;background-position:center;background-size:cover;transition:all .2s;overflow:hidden}.card:hover{-webkit-transform:scale(1.03);transform:scale(1.03)}.card__content{height:100%;width:100%;top:-102.5%;padding:0 1rem}.card__content>*{padding-top:0.3rem}.card__content__description{font-weight:20}.card__content__references{font-size:1.2rem;display:flex;justify-content:center}.card__content__references-item{padding:0.3rem;display:flex;align-content:center}.card__content__references-item-logo{align-self:center}.card__content__references-item-text{padding-left:0.3rem}.card__content__references-item:last-child{margin-left:1rem}.card__content__references-item:hover{cursor:pointer;background-color:rgba(245,245,245,0.694);border-radius:3px}.card__content__technologies{justify-content:center;display:flex;flex-direction:row}.card__content__technologies-item{padding:0.3rem}.card__content__technologies-item:not(:first-child){margin-left:1rem}.opacity{opacity:0}.opacity:hover{background-color:rgba(0,0,0,0.6);border-radius:3px;height:100%;width:100%;opacity:1}

